*{
    /* border: 1px solid red; */
    font-family: aktiv-grotesk, sans-serif;
    font-style: normal;
    font-size: 20px;
}

body{
    margin: 0;
    padding: 0;
    text-align: center;
    color: white;
}

#main-container{
    display: block;
    margin: auto;
    width: 50%;
    background-color:slategray;
    padding: 1%;
    margin-top: 10px;
    border-radius: 10px;
}

#h1{
    text-align: center;
    font-size: 36px;
    display: block;
}

#text-input{
    width: 50%;
    font-size: 20px;
    border: none;
    border-radius: 5px;
}

#add-button{
    width: 15%;
    color: slategray;
    border: none;
    border-radius: 5px;
    font-size: 20px;
}

#list-items{
    padding: 0px 20% 0px 20%;
    font-size: 20px;
}

.list-item{
    font-weight: 700;
    list-style-type: none;
    text-align: left;
    margin:20px 0px 20px 0px;
}

.delete-button{
    align-items: left;
    border: none;
    border-radius: 5px;
    display: block;
    margin-top: 5px;
    color: slategray;
}

.checked{
    font-weight: 300;
    color: darkgrey;
}



